/*------------------------------------------
 CMB2 Interface
--------------------------------------------*/

.cmb2-metabox-description {
  display: block;
  margin-top: 5px;
}

.cmb2-id-gmb-places-search-multicheckbox {
  ul {
	overflow: hidden;
  }
  li {
	float: left;
	width: 33%;
  }
  .cmb2-metabox-description {
	margin-top: 10px;
	margin-right: 2px;
  }
}



/*-------------------------------------
Autocomplete Metabox
---------------------------------------*/
#cmb2-metabox-google_maps_markers {

  .search-autocomplete {
	width: 78%;
	margin-right: 10px;
  }
  .autocomplete-wrap p {
	font-style: italic;
	margin: 5px 0;
	color: #aaaaaa;
  }
  a.drop-marker {
	font-style: normal;
  }
  .autocomplete-wrap {
    border-bottom: 1px solid #EEE;
    padding: 0 0 12px;
  }
}


/*-------------------------------------

---------------------------------------*/

#google-map-wrap {
  position: relative;
}

#poststuff .postbox-container {
  .cmb-repeatable-group:not(:last-of-type) {
	border-bottom: 1px solid #e9e9e9;
	padding: 0 0 15px;
  }
  .cmb-repeat-group-wrap .cmb-row:not(:last-of-type) {
	margin-bottom: 10px;
  }
}

#gmb_markers_group_repeat.cmb-remove-field-row {
  margin-bottom: 15px;
}

#cmb2-metabox-google_maps_markers {
  .cmb-repeatable-grouping {
	padding-top: 0;
	border-bottom: 1px solid #e9e9e9 !important;
	.cmb-remove-field-row {
	  padding-bottom: 12px !important;
	}
  }
}

//Tables
#poststuff .inner-sidebar, #poststuff #side-sortables {

  .inside {
	margin-top: 0;
	padding-top: 12px;
  }

  p.cmb2-metabox-description {
	padding: 0;
	font-weight: normal;
  }

  .cmb-row {
	margin: 0;
	padding: 0;
	margin-bottom: 6px;

	&:last-child {
	  margin-bottom: 0;
	}
  }

  .cmb-th {
	background: transparent;
	padding: 0;
	position: relative;

	label {
	  font-weight: normal;
	  padding: 6px 8px 10px 0;
	}

  }

  .cmb-td {
	//repeater
	&.cmb-nested {
	  padding-left: 12px;
	  padding-right: 12px;
	}
  }

  //Sidebar repeater
  #gmb_markers_group_repeat {
	.cmb-group-title {
	  margin: 0;
	  padding: 6px 8px 6px 30px;
	  font-weight: normal;
	  cursor: pointer;
	}

	.cmb-field-list {
	  border-left: 1px solid #EEE;
	  border-right: 1px solid #EEE;
	}

	.cmb-remove-row {
	  margin: 0;
	  padding: 0;
	}

	.cmb-remove-group-row {
	  top: 4px;
	}

	.cmbhandle {
	  right: 0;
	  top: -2px;
	}
	.cmb-add-row {
	  margin: 15px 0 10px;
	}

  }

}

//Non-magnific Builder Repeater styles
#postbox-container-2 #cmb2-metabox-google_maps_markers {

  .toggle-repeater-groups {
	float: right;
  }

  .cmb-repeat-group-wrap {
	padding: 0;
  }

  .cmb-group-description .cmb-th {
	padding-right: 0;
  }

}

p.cmb2-metabox-description {
  font-weight: normal;
}

a.wp-color-result {
  height:24px;
}